
.cell_title {
    font-size: .8em;
}
.cell_title .reply_count {
    color: #9e78c0;
}
.cell_title .visit_count {
    color: #b4b4b4
}
.cell_title .top {
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
}
.cell_title .tab {
    background-color: #e5e5e5;
    color: #999;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    font-size: 12px;
}
.cell_ceauthor_avatar img {
    width: 30px;
    height: 30px;
    border-radius: 3px;
    margin-right: 5px;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
}



